
"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";

export default function TickerCompareInput() {
  const [input, setInput] = useState("");
  const router = useRouter();

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const tickers = input
      .toUpperCase()
      .replace(/[^A-Z,\s]/g, "")
      .split(/[\s,]+/)
      .filter(Boolean);
    if (tickers.length > 1) {
      router.push(`/compare/${tickers.join(",")}`);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-x-2 flex justify-center">
      <input
        type="text"
        placeholder="Compare AAPL, MSFT, GOOGL"
        value={input}
        onChange={(e) => setInput(e.target.value)}
        className="bg-zinc-800 text-white p-2 rounded-lg w-96"
      />
      <button type="submit" className="bg-blue-600 text-white px-4 py-2 rounded-lg">
        Compare
      </button>
    </form>
  );
}
